import {Component} from '@angular/core';
import { NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {DIALOG_SUBTITTLE, DIALOG_TITLE} from "./crypto-balance-fill.constants";
import { applyTemplate } from "../../utils";

@Component({
  selector: 'ngbd-modal-crypto-fill',
  templateUrl: './crypto-balance-fill.component.html'
})
export class CryptoBalanceFillComponent {
  subtitle: string;
  currencyName: string;
  constructor(private _activeModal: NgbActiveModal) {
    this.subtitle = DIALOG_SUBTITTLE;
  }
  public onDismiss(){
    this._activeModal.dismiss('click on close')
  }

  public get title(){
    return applyTemplate(DIALOG_TITLE, {currencyName: this.currencyName});
  }
}



