import {ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {IBalanceItem} from './balance.interfaces';
import {BalanceService} from './balance.service';
import {SubscriberComponent} from "../subscriber/subscriber.component";

@Component({
  selector: 'layout-balance',
  templateUrl: './balance.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class BalanceComponent extends SubscriberComponent implements OnInit {
  public balanceItems: IBalanceItem[];
  constructor(private _balanceService: BalanceService, private _detectorRef: ChangeDetectorRef) {
    super();
  }

  ngOnInit() {
    this.subscriptions.push(this._balanceService.balance$.subscribe((result: IBalanceItem[]) => {
      this.balanceItems = result;
      this._detectorRef.markForCheck();
    }));
  }

}



