import {Injectable} from '@angular/core';
import {Store} from '@ngrx/store';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/distinctUntilChanged';
import {ICurrencyTablet} from './currency-tablet.interfaces';
import {FetchCurrencyTabletLoadingAction, UpdateCurrencyTabletAction} from './currency-tablet.actions';
import {TABLETS} from './currency-tablet-mock-data';


@Injectable()
export class CurrencyTabletService {
  public carrencyTablets$: Observable<ICurrencyTablet[]>;
  public carrencyTabletsLoading$: Observable<boolean>;
  constructor(private _store: Store<any>) {
    const carrencyTabletsState$ = this._store.select('currencyTablet');
    this.carrencyTablets$ = carrencyTabletsState$.map((x) => x.data).distinctUntilChanged();
    this.getBalance();
  }

  public getBalance() {
    //http request for data
    this._store.dispatch(new FetchCurrencyTabletLoadingAction(true));
    setTimeout(() => {
      this._store.dispatch(new UpdateCurrencyTabletAction(TABLETS));
      this._store.dispatch(new FetchCurrencyTabletLoadingAction(false));
    }, 1000);

  }
}



