import {ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {TransactionService, ITransaction} from '../../transaction';
import {Observable} from 'rxjs/Observable';
import {ScreenService} from '../../screen';
import {LayoutService, Screen} from '../../layout';
import {SubscriberComponent} from "../../subscriber/subscriber.component";

@Component({
  selector: 'transactions-tab',
  templateUrl: './transactions-tab.component.html',
  styleUrls: ['./transactions-tab.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TransactionsTabComponent extends SubscriberComponent implements OnInit {
  screen: Screen;
  transactions$: Observable<ITransaction[]>;

  constructor(private _tranService: TransactionService, private  _screenService: ScreenService,
              private _layoutService: LayoutService,
              private _detectorRef: ChangeDetectorRef) {

    super();
  }

  ngOnInit() {
    this.subscriptions.push(this._layoutService.screen$.subscribe((screen) => {
      this.screen = screen;
      this._detectorRef.markForCheck();
    }));
    this.transactions$ = this._tranService.transactions$;
  }

  public get isMobileScreen(): boolean {
    return this._screenService.isMobileScreen(this.screen);
  }
}



