import {IExchangeRateItem} from './exchange-rate.interfaces';
import * as exchangeRate from './exchange-rate.actions';

export interface IExchangeRateState {
  data: IExchangeRateItem[],
  loading: boolean
}

export const initialState: IExchangeRateState = {
  data: null,
  loading: false
};

export function reducer(state = initialState,
                        action: exchangeRate.Actions): IExchangeRateState {
  switch (action.type) {
    case exchangeRate.UPDATE_EXCHANGE_RATE: {
       return Object.assign({}, state, {data: action.payload});
    }
    case exchangeRate.FETCH_EXCHANGE_RATE: {
      return Object.assign({}, state, {loading: action.payload});
    }
    default: {
      return state;
    }
  }
}



