export interface ICreateUserRequest {
  firstName:	string;
  lastName:	string;
  email:	string;
  phoneNumber:	string;
  password:	string;
}

export interface IConfirmPhoneRequest {
  confirmationCode:	string;
}

export interface IGetConfirmCodeRequest {
  actionType:	ActionType;
}

export interface IGetConfirmCodeResponse {
  sendingExpireDate: number; //unix format
}

export enum ActionType {
  NewEmail = 'NewEmail',
  NewPhone = 'NewPhone'
}



