import {Component, Input, OnInit} from '@angular/core';
import {ICurrencyTablet} from '../currency-tablet.interfaces';
import  {CURRENCY_IMAGE_URL_128} from '../../../../constants';
import  {applyTemplate} from '../../../../utils';
import {CURRENCY_DESCRIPTION_CLASS} from './currency-tablet-item.constants';

@Component({
  selector: 'currency-tablet-item',
  templateUrl: './currency-tablet-item.component.html',
  styleUrls: ['./currency-tablet-item.component.scss']
})
export class CurrencyTabletItemComponent implements OnInit {
  @Input() tablet: ICurrencyTablet;

  constructor() {
  }

  ngOnInit() {
  }

  public get srcUrl(): string {
    return applyTemplate(CURRENCY_IMAGE_URL_128, {currencyName: this.tablet.type});
  }

  public get currencyClassName(): string {
    return applyTemplate(CURRENCY_DESCRIPTION_CLASS, {currencyName: this.tablet.type});
  }

}



