import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import {CommonModule} from "@angular/common";
import { ReactiveFormsModule } from '@angular/forms';
import {RouterModule} from '@angular/router';
import {LoaderModule} from "../../loader";
import {PhoneConfirmationComponent} from "./phone-confirmation.component";

@NgModule({
  imports: [RouterModule, ReactiveFormsModule, CommonModule, HttpClientModule, LoaderModule],
  declarations: [
    PhoneConfirmationComponent
  ],
  exports: [
    PhoneConfirmationComponent
  ]
})
export class PhoneConfirmationModule { }



