import {Component, Input, OnInit} from '@angular/core';
import {IBalanceItem} from '../balance.interfaces';
import {CURRENCY_IMAGE_URL_16, DECIMAL_FORMAT} from '../../constants';
import {applyTemplate} from "../../utils";


@Component({
  selector: 'layout-balance-item',
  templateUrl: './balance-item.component.html'
})
export class BalanceItemComponent {

  @Input() balanceItem: IBalanceItem;

  public decimalFormat = DECIMAL_FORMAT;


  public get srcUrl(): string {
    return applyTemplate(CURRENCY_IMAGE_URL_16, {currencyName: this.balanceItem.type});
  }
}



