import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {MainComponent} from './main.component';

import {AuthGuard} from '../../services/guards/auth.guard.service';
import {ControlPanelTabComponent, ControlPanelTabModule} from '../../tabs/control-panel';
import {TransactionsTabComponent, TransactionsTabModule} from '../../tabs/transactions';
import {WalletsTabComponent, WalletsTabModule} from '../../tabs/wallets';
import {SettingsTabComponent, SettingsTabModule} from '../../tabs/settings';
import {FaqTabComponent, FaqTabModule} from '../../tabs/faq';


const mainRoutes: Routes = [
  {
    path: '', component: MainComponent, canActivate: [AuthGuard],
    children: [
      {path: 'control-panel', component: ControlPanelTabComponent},
      {path: 'transactions', component: TransactionsTabComponent},
      {path: 'wallets', component: WalletsTabComponent},
      {path: 'settings', component: SettingsTabComponent},
      {path: 'faq', component: FaqTabComponent},
    ]
  }
];

@NgModule({
  imports: [
    ControlPanelTabModule,
    TransactionsTabModule,
    WalletsTabModule,
    SettingsTabModule,
    FaqTabModule,
    RouterModule.forChild(mainRoutes)
  ],
  providers: [AuthGuard],
  exports: [
    RouterModule
  ]
})
export class MainRoutingModule {
}



