import { Screen} from "./layout.interfaces";
import * as layout from './layout.actions';

export interface ILayoutState {
  screen: Screen
}

export const initialState: ILayoutState = {
  screen: null
};


export function reducer(state = initialState,
                        action: layout.Actions): ILayoutState {
  switch (action.type) {
    case layout.UPDATE_SCREEN: {
       return Object.assign({}, state, {screen: action.payload});
    }

    default: {
      return state;
    }
  }
}



