import { Action } from '@ngrx/store';
import {IExchangeRateItem} from './exchange-rate.interfaces';

export const UPDATE_EXCHANGE_RATE =           '[ExchangeRate] update';
export const FETCH_EXCHANGE_RATE =           '[ExchangeRate] fetch';

export class UpdateExchangeRateAction implements Action {
  readonly type = UPDATE_EXCHANGE_RATE

  constructor(public payload: IExchangeRateItem[]) { }
}

export class FetchExchangeRateLoadingAction implements Action {
  readonly type = FETCH_EXCHANGE_RATE;

  constructor(public payload: boolean = true) { }
}

export type Actions
  = UpdateExchangeRateAction
  | FetchExchangeRateLoadingAction;



