import { Component, OnInit, Input, ChangeDetectionStrategy } from '@angular/core';
import {ITransaction} from '../../../../transaction';
import {DATE_FORMAT, DECIMAL_FORMAT} from "../../../../constants";

@Component({
  selector: '[transactions-table-row]',
  templateUrl: './transactions-table-row.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TransactionsTableRowComponent implements OnInit {
  public columnNames: string[];
  public decimalFormat = DECIMAL_FORMAT;
  public dateFormat = DATE_FORMAT;

  @Input() rowItem: ITransaction;

  constructor() { }

  ngOnInit() {
    this.columnNames = Object.keys(this.rowItem);
  }
}



