import {ChangeDetectionStrategy, Component, OnInit, Input, SimpleChanges} from '@angular/core';
import {ITransaction} from '../../../transaction/transaction.interfaces';

@Component({
  selector: 'transactions-table',
  templateUrl: './transactions-table.component.html',
  styleUrls: ['./transactions-table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TransactionsTableComponent implements OnInit {

  columns: string[];
  @Input() rows: ITransaction[];

  constructor() { }

  ngOnInit() {
    this.columns = ['Дата', 'Валюта', 'Сумма', 'Тип операции', 'Статус'];
  }

}



