import {ITransaction} from './transaction.interfaces';
import * as transaction from './transaction.actions';

export interface ITransactionsState {
  data: ITransaction[],
  loading: boolean
}

export const initialState: ITransactionsState = {
  data: null,
  loading: false
};


export function reducer(state = initialState,
                        action: transaction.Actions): ITransactionsState {
  switch (action.type) {
    case transaction.UPDATE_TRANSACTIONS: {
       return Object.assign({}, state, {data: action.payload});
    }
    case transaction.FETCH_TRANSACTIONS: {
      return Object.assign({}, state, {loading: action.payload});
    }
    default: {
      return state;
    }
  }
}




