import {Injectable} from "@angular/core";
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {API_URLS} from "../../constants";
import {ICreateUserRequest, IConfirmPhoneRequest, IGetConfirmCodeRequest} from './registration.interfaces';

@Injectable()
export class RegistrationService {
  constructor(private _http: HttpClient) {}
  createUser(user: ICreateUserRequest){
    return this._http.post(API_URLS.REGISTRATION, JSON.stringify(user));
  }

  confirmByPhone(confirmCodeReq: IConfirmPhoneRequest) {
    return this._http.post(API_URLS.CONFIRM_PHONE, JSON.stringify(confirmCodeReq));
  }

  sendRetrieveConfirmCodeRequest(getCodeReq: IGetConfirmCodeRequest) {
    return this._http.post(API_URLS.RESEND_CONFIRM_CODE, JSON.stringify(getCodeReq));
  }
}



