import {ChangeDetectionStrategy, Component, OnInit} from '@angular/core';
import {ICurrencyTablet} from './currency-tablet.interfaces';
import {CurrencyTabletService} from './currency-tablet.service';
import {Observable} from 'rxjs/Observable';


@Component({
  selector: 'currency-tablet',
  templateUrl: './currency-tablet.component.html',
  styleUrls: ['./currency-tablet.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CurrencyTabletComponent implements OnInit {

  public currencyTablets$: Observable<ICurrencyTablet[]>;
  constructor(private _currencyTabletService: CurrencyTabletService) {

  }

  ngOnInit() {
    this.currencyTablets$ = this._currencyTabletService.carrencyTablets$;
  }

}



