import {Injectable} from '@angular/core';
import {
  CanActivate, Router,
  ActivatedRouteSnapshot,
  RouterStateSnapshot,
  CanActivateChild,
  NavigationExtras,
  CanLoad, Route
} from '@angular/router';
import {IUser, UserService} from "../../user/index";
import {Observable} from "rxjs/Observable";
import 'rxjs/add/observable/combineLatest';
import 'rxjs/add/operator/skipWhile';
import {getValue} from "../../utils";

@Injectable()
export class AuthGuard implements CanActivate {
  constructor(private _userService: UserService, private _router: Router) {
  }

  public canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean | Observable<boolean> {
    if(getValue(this._userService.user$) === null && !getValue(this._userService.userLoading$)) {
      this._userService.getUser();
    }
    return Observable.combineLatest(
      this._userService.user$,
      this._userService.userLoading$)
      .skipWhile(([user, loading]) => {
        return loading;
      }).map(([user, loading]) => {
        if (!user) {
          this._router.navigate(['/login']);
        }
        else if(!user.isPhoneConfirmed) {
          this._router.navigate(['/confirm-phone']);
        }
        return (!!user && user.isPhoneConfirmed);
      });
  }
}



