import {ICurrencyTablet} from './currency-tablet.interfaces';
import * as currencyTablet from './currency-tablet.actions';

export interface ICurrencyTabletState {
  data: ICurrencyTablet[],
  loading: boolean
}

export const initialState: ICurrencyTabletState = {
  data: null,
  loading: false
};

export function reducer(state = initialState,
                        action: currencyTablet.Actions): ICurrencyTabletState {
  switch (action.type) {
    case currencyTablet.FETCH_CURRENCY_TABLET: {
      return Object.assign({}, state, {loading: action.payload});
    }
    case currencyTablet.UPDATE_CURRENCY_TABLET: {
      return Object.assign({}, state, {data: action.payload});
    }
    default: {
      return state;
    }
  }
}



