import { ChangeDetectionStrategy, Component, ChangeDetectorRef } from '@angular/core';
import { RegistrationService } from './registration.service';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { ICreateUserRequest } from './registration.interfaces';
import { AuthService } from '../../services/auth/auth.service';
import { ITokenScheme } from '../../services/auth/auth.interfaces';
import { IUser, UserService } from '../../user';
import { Router } from '@angular/router';
import { CONFLICT_ERROR_MESSAGE, WRONG_FORMAT_ERROR_MESSAGE } from './registration.constants';
import 'rxjs/add/operator/finally';
import { SubscriberComponent } from "../../subscriber/subscriber.component";
import { SERVER_ERROR_MESSAGE } from '../../constants';

@Component({
  selector: 'registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RegistrationComponent extends SubscriberComponent {
  form: FormGroup;
  serverErrorTitle: string;
  loading: boolean;

  constructor(private _registrationService: RegistrationService,
    private _authService: AuthService,
    private _userService: UserService,
    private _router: Router,
    private fb: FormBuilder,
    private _detectorRef: ChangeDetectorRef) {
    super();
    this._createForm();
  }

  get firstName() {
    return this.form.get('firstName');
  }

  get lastName() {
    return this.form.get('lastName');
  }

  get email() {
    return this.form.get('email');
  }

  get password() {
    return this.form.get('password');
  }

  get confirmPassword() {
    return this.form.get('confirmPassword');
  }

  get phoneNumber() {
    return this.form.get('phoneNumber');
  }

  public createUser() {
    if (this.form.valid && !this.loading) {
      const userRequest: ICreateUserRequest = {
        firstName: this.form.value.firstName,
        lastName: this.form.value.lastName,
        email: this.form.value.email,
        phoneNumber: this.form.value.phoneNumber,
        password: this.form.value.password
      };
      this.loading = true;
      this._registrationService.createUser(userRequest).finally(() => {
        this.loading = false;
        this._detectorRef.detectChanges();
      }).subscribe((data: ITokenScheme) => {
        this._authService.storeTokens(data);
        this._router.navigate(['/']);
      },
        (err) => {
          if (err.status === 500) {
            this.serverErrorTitle = SERVER_ERROR_MESSAGE;
          } else {
            this.serverErrorTitle = err.error.description;
          }
        });
    }
  }

  goToLogin(){
    this._router.navigate(['/login']);
  }

  private _createForm() {
    this.form = this.fb.group({
      firstName: ['', [Validators.required, Validators.minLength(2)]],
      lastName: ['', [Validators.required, Validators.minLength(2)]],
      email: ['', [Validators.email]],
      password: ['', [Validators.required, Validators.minLength, Validators.minLength(8)]],
      confirmPassword: ['', [Validators.required, this._validateAreEqual.bind(this)]],
      phoneNumber: ['', [Validators.required, Validators.pattern('^\\+7[0-9]{10,14}$')]]
    });

    this._subscribeOnPasswordChanges();
  }

  private _validateAreEqual(fieldControl: FormControl) {
    if (this.form) {
      return this.confirmPassword.value === this.password.value ? null : {
        notEqual: true
      };
    }
  }

  private _subscribeOnPasswordChanges() {
    this.subscriptions.push(this.password.valueChanges.subscribe(() => {
      this.confirmPassword.updateValueAndValidity();
    }));
  }

}



