import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {CommonModule, CurrencyPipe, DatePipe, DecimalPipe} from '@angular/common';


import {AppComponent} from './app.component';
import {MainComponentModule} from './pages/main';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {SettingsTabModule} from './tabs/settings';
import {FaqTabModule} from './tabs/faq';
import {StoreModule} from '@ngrx/store';
import {reducers} from './store';
import {UserService} from './user';
import {TransactionService} from './transaction';
import {SidebarModule} from "ng-sidebar";
import {LayoutService} from "./layout";
import {AppRoutingModule} from "./app-routing.module";
import {RegistrationModule} from "./pages/registration";
import {ApplyTokenInterceptor} from "./interceptors/apply-token.interceptor";
import {RefreshTokenInterceptor} from "./interceptors/refresh-token.interceptor";
import {HTTP_INTERCEPTORS} from "@angular/common/http";
import {AuthService} from "./services/auth/auth.service";
import {LoginModule} from "./pages/login/login.module";
import {JsonInterceptor} from "./interceptors/json.interceptor";


import {BalanceService} from './balance';
import {ExchangeRateService} from './exchange-rate';
import {CurrencyTabletService} from './tabs/control-panel/currency-tablet/currency-tablet.service';
import {LoaderModule} from "./loader/loader.module";
import {PhoneConfirmationModule} from "./pages/confirmation";


@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    SidebarModule.forRoot(),
    StoreModule.forRoot(reducers),
    SettingsTabModule,
    FaqTabModule,
    RegistrationModule,
    LoginModule,
    PhoneConfirmationModule,
    RouterModule.forRoot([]),
    NgbModule.forRoot(),
    BrowserModule,
    MainComponentModule,
    AppRoutingModule,
    CommonModule,
    LoaderModule
  ],
  providers: [
    UserService,
    AuthService,
    LayoutService,
    TransactionService,
    DatePipe,
    DecimalPipe,
    CurrencyPipe,
    BalanceService,
    ExchangeRateService,
    CurrencyTabletService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: JsonInterceptor,
      multi: true,
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: ApplyTokenInterceptor,
      multi: true,
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: RefreshTokenInterceptor,
      multi: true,
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}



