import {Injectable} from '@angular/core';
import {Store} from '@ngrx/store';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/distinctUntilChanged';
import {IBalanceItem} from './balance.interfaces';
import {FetchBalanceLoadingAction, UpdateBalanceAction} from './balance.actions';
import {BALANCES} from './balance-mock-data';


@Injectable()
export class BalanceService {
  public balance$: Observable<IBalanceItem[]>;
  public balanceLoading$: Observable<boolean>;
  constructor(private _store: Store<any>) {
    const balanceState$ = this._store.select('balance');
    this.balance$ = balanceState$.map((x) => x.data).distinctUntilChanged();
    this.getBalance();
  }

  public getBalance() {
    //http request for data
    this._store.dispatch(new FetchBalanceLoadingAction(true));
    setTimeout(() => {
      this._store.dispatch(new UpdateBalanceAction(BALANCES));
      this._store.dispatch(new FetchBalanceLoadingAction(false));
    }, 1000);

  }
}



