import {ChangeDetectorRef, Component, Input, OnInit} from '@angular/core';
import {LayoutService, Screen} from '../layout';
import {ScreenService} from '../screen';
import {SubscriberComponent} from "../subscriber/subscriber.component";

@Component({
  selector: 'tab-component',
  templateUrl: './tab.component.html',
  styleUrls: ['./tab.component.scss']
})
export class TabComponent extends SubscriberComponent implements OnInit {
  screen: Screen;

  constructor(private  _screenService: ScreenService,
              private _layoutService: LayoutService,
              private _detectorRef: ChangeDetectorRef) {

    super();
  }

  public ngOnInit() {

    this.subscriptions.push(this._layoutService.screen$.subscribe((screen) => {
      this.screen = screen;
      this._detectorRef.markForCheck();
    }));
  }

  public get isMobileScreen(): boolean {
    return this._screenService.isMobileScreen(this.screen);
  }
}



