import { Action } from '@ngrx/store';
import {ICurrencyTablet} from './currency-tablet.interfaces';

export const UPDATE_CURRENCY_TABLET =           '[CurrencyTablet] update';
export const FETCH_CURRENCY_TABLET =           '[CurrencyTablet] fetch';

export class UpdateCurrencyTabletAction implements Action {
  readonly type = UPDATE_CURRENCY_TABLET

  constructor(public payload: ICurrencyTablet[]) { }
}

export class FetchCurrencyTabletLoadingAction implements Action {
  readonly type = FETCH_CURRENCY_TABLET;

  constructor(public payload: boolean = true) { }
}

export type Actions
  = UpdateCurrencyTabletAction
  | FetchCurrencyTabletLoadingAction;



