import {Injectable} from '@angular/core';
import {Store} from '@ngrx/store';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/distinctUntilChanged';
import {IUser} from './user.interfaces';
import {FetchUserLoadingAction, UpdateUserAction} from './user.actions';
import {IUserState} from './user.reducers';
import {HttpClient} from '@angular/common/http';
import {API_URLS} from '../constants';
import 'rxjs/add/operator/finally';
import {getValue} from "../utils";

@Injectable()
export class UserService {
  public user$: Observable<IUser>;
  public userLoading$: Observable<boolean>;

  constructor(private _store: Store<any>,
              private _http: HttpClient) {
    const userState$ = this._store.select('user');
    this.user$ = userState$.map((x: IUserState) => x.data).distinctUntilChanged();
    this.userLoading$ = userState$.map((x: IUserState) => x.loading).distinctUntilChanged();

  }

  public updateUser(user: IUser) {
    this._store.dispatch(new UpdateUserAction(user));
  }
   public confirmUserPhone(){
    let user = getValue(this.user$);
    if(user !== null) {
      this.updateUser(Object.assign({}, user, {isPhoneConfirmed: true}));
    }
   }
  public getUser() {
    this._store.dispatch(new FetchUserLoadingAction(true));

    this._http.get(API_URLS.GET_USER)
      .finally(() => {
        this._store.dispatch(new FetchUserLoadingAction(false));
      })
      .subscribe((data: IUser) => {
          this._store.dispatch(new UpdateUserAction(data));
        },
        (err) => {
          this._store.dispatch(new UpdateUserAction(null));
        });
  }
}



