import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  HostListener,
  ViewChild
} from '@angular/core';
import {LayoutService, Screen} from "../layout";


@Component({
  selector: 'screen',
  templateUrl: './screen.component.html',
  styleUrls: ['./screen.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ScreenComponent {
  @ViewChild('xs' , {read: ElementRef}) public xs;
  @ViewChild('sm' , {read: ElementRef}) public sm;
  @ViewChild('md' , {read: ElementRef}) public md;
  @ViewChild('lg' , {read: ElementRef}) public lg;
  @ViewChild('xl' , {read: ElementRef}) public xl;
  constructor(private _layout: LayoutService) {
  }
  ngOnInit(){
    this._onResize();
  }
  @HostListener('window:resize')
  private _onResize(){
    if(this._isVisible(this.xs.nativeElement)){
      this._layout.updateScreen(Screen.xs);
      return;
    }
    if(this._isVisible(this.sm.nativeElement)){
      this._layout.updateScreen(Screen.sm);
      return;
    }
    if(this._isVisible(this.md.nativeElement)){
      this._layout.updateScreen(Screen.md);
      return;
    }
    if(this._isVisible(this.lg.nativeElement)){
      this._layout.updateScreen(Screen.lg);
      return;
    }
    if(this._isVisible(this.xl.nativeElement)){
      this._layout.updateScreen(Screen.xl);
    }
  }

  private _isVisible(elem: any){
    return  !( elem.offsetWidth <= 0 && elem.offsetHeight <= 0 &&
      !elem.getClientRects().length);
  }
}



