import {ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {LayoutService, Screen} from "../../layout";
import {Observable} from "rxjs/Observable";
import {createChangeDetectorRef} from "@angular/core/src/view/refs";
import {ScreenService} from "../../screen";
import {HttpClient} from "@angular/common/http";
import {API_URLS} from "../../constants";
import {SubscriberComponent} from "../../subscriber/subscriber.component";
import {Router} from "@angular/router";

@Component({
  selector: 'main-page',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class MainComponent extends SubscriberComponent implements OnInit {
  public screen: Screen;

  private _sidebarOpened: boolean = false;

  constructor(private _layoutService: LayoutService,
              private _screen: ScreenService,
              private _detectorRef: ChangeDetectorRef,
              private _http: HttpClient,
              private _router: Router) {
    super();
  }

  public ngOnInit() {
    // setTimeout(() => {
    //   this._http.get(API_URLS.METADATA).subscribe((data) => {
    //   })
    // }, 10000);
    this._router.navigate(['/control-panel']);
    this.subscriptions.push(this._layoutService.screen$.subscribe((screen) => {
      this.screen = screen;
      this._detectorRef.markForCheck();
    }));
  }

  public get sidebarMode(): string {
    return !this._screen.isMobileScreen(this.screen) ? 'push' : 'over';
  }

  public get sidebarOpened(): boolean {
    return this._screen.isMobileScreen(this.screen) ? this._sidebarOpened : true
  }

  public sidebarVisibilityChange(opened: boolean) {
    this._sidebarOpened = opened;
  }
}



