import { Action } from '@ngrx/store';
import {ITransaction} from './transaction.interfaces';

export const UPDATE_TRANSACTIONS =           '[Transactions] update';
export const FETCH_TRANSACTIONS =           '[Transactions] fetch';

export class UpdateTransactionsAction implements Action {
  readonly type = UPDATE_TRANSACTIONS;

  constructor(public payload: ITransaction[]) { }
}

export class FetchTransactionsLoadingAction implements Action {
  readonly type = FETCH_TRANSACTIONS;

  constructor(public payload: boolean = true) { }
}


export type Actions
  = UpdateTransactionsAction
  | FetchTransactionsLoadingAction;



