import {ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Observable} from "rxjs/Observable";
import {IUser, UserService} from "../user";
import {LayoutService, Screen} from "../layout";
import {ScreenService} from "../screen";
import {NgbModal} from "@ng-bootstrap/ng-bootstrap";
import {CryptoBalanceFillComponent} from "../modals/crypto-balance-fill/crypto-balance-fill.component";

@Component({
  selector: 'header-component',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class HeaderComponent implements OnInit {
  @Output() sidebarButtonClick = new EventEmitter<boolean>();
  @Input() sidebarOpened: boolean = false;
  @Input() screen: Screen;
  public user$: Observable<IUser>;
  constructor(private _userService: UserService,
              private _layout: LayoutService,
              private _screen: ScreenService,
              private _modalService: NgbModal) {

  }

  ngOnInit() {
    this.user$ = this._userService.user$;
  }
  public get isMobileScreen(): boolean {
    return this._screen.isMobileScreen(this.screen);
  }
  sidebarVisibilityChange() {
    this.sidebarButtonClick.emit(!this.sidebarOpened);
  }


  open() {
    const modalRef = this._modalService.open(CryptoBalanceFillComponent).result.then((result)=>{
      console.log('result success', result)
    }, (e)=>{
      console.log('result error', e);
    });
  }
}





