import {observable} from "rxjs/symbol/observable";
import {Observable} from "rxjs/Observable";

export const applyTemplate = (template: string, replacements: any, encode: boolean = true): string => {
  return template.replace(/{(\w+)}/g, function (e, n) {
    return undefined !== replacements[n] ? (encode ? encodeURIComponent(replacements[n]) : replacements[n]) : "";
  });
};
export const getValue = <T>(observable: Observable<T>): T => {
  let value: T;
  const subscription = observable
    .subscribe((x) => {
      value = x;
    });
  subscription.unsubscribe();
  return value;
};




