import {Injectable} from '@angular/core';
import {Store} from '@ngrx/store';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/distinctUntilChanged';
import {ITransaction} from './transaction.interfaces';
import {FetchTransactionsLoadingAction, UpdateTransactionsAction} from './transaction.actions';
import { TRANSACTIONS } from './transactions-mock-data';


@Injectable()
export class TransactionService {
  public transactions$: Observable<ITransaction[]>;
  public transactionsLoading$: Observable<boolean>;


  constructor(private _store: Store<any>) {
    const transactionsState$ = this._store.select('transactions');
    console.log(transactionsState$)
    this.transactions$ = transactionsState$.map((x) => x.data).filter(data => data !== null).distinctUntilChanged();
    this.getTransactions();
  }

  public getTransactions(){
    this._store.dispatch(new FetchTransactionsLoadingAction(true));
    setTimeout(()=> {
      this._store.dispatch(new UpdateTransactionsAction(TRANSACTIONS));

      this._store.dispatch(new FetchTransactionsLoadingAction(false));
    }, 1000)
  }
}



