import * as fromUser from './user';
import * as fromTransaction from './transaction';
import * as fromLayout from './layout';
import * as fromBalance from './balance';
import * as fromExchangeRate from './exchange-rate';
import * as fromCurrencyTablet from './tabs/control-panel';



export interface State {
  user: fromUser.IUserState;
  transactions: fromTransaction.ITransactionsState;
  layout: fromLayout.ILayoutState;
  balance: fromBalance.IBalanceState;
  exchangeRate: fromExchangeRate.IExchangeRateState;
  currencyTablet: fromCurrencyTablet.ICurrencyTablet;
}

export const reducers = {
  user: fromUser.reducer,
  transactions: fromTransaction.reducer,
  layout: fromLayout.reducer,
  balance: fromBalance.reducer,
  exchangeRate: fromExchangeRate.reducer,
  currencyTablet: fromCurrencyTablet.reducer
};





