import {Injectable} from "@angular/core";
import {Store} from "@ngrx/store";
import {Observable} from "rxjs/Observable";
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/distinctUntilChanged';
import {Screen} from "./layout.interfaces";
import {UpdateScreenAction} from "./layout.actions";



@Injectable()
export class LayoutService {
  public screen$: Observable<Screen>;
  public userLoading$: Observable<boolean>;
  private _lastUpdatedScreen: Screen = null;
  constructor(private _store: Store<any>) {
    const layoutState$ = this._store.select('layout');
    this.screen$ = layoutState$.map((x)=>x.screen).distinctUntilChanged();
  }
  public updateScreen(screen: Screen) {
    if(this._lastUpdatedScreen !== screen) {
      this._store.dispatch(new UpdateScreenAction(screen));
      this._lastUpdatedScreen = screen;
    }
  }
}



