import {Component, Input, OnInit} from '@angular/core';
import {IExchangeRateItem} from '../exchange-rate.interfaces';
import {CURRENCY_IMAGE_URL_16, DECIMAL_FORMAT} from "../../constants";
import {applyTemplate} from "../../utils";


@Component({
  selector: 'exchange-rate-item',
  templateUrl: './exchange-rate-item.component.html'
})
export class ExchangeRateItemComponent {

  @Input() exchangeRateItem: IExchangeRateItem;
  public decimalFormat = DECIMAL_FORMAT;


  public get srcUrl(): string {
    return applyTemplate(CURRENCY_IMAGE_URL_16, {currencyName: this.exchangeRateItem.type});
  }
}



