import {ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {IExchangeRateItem} from './exchange-rate.interfaces';
import {ExchangeRateService} from './exchange-rate.service';
import {SubscriberComponent} from "../subscriber/subscriber.component";

@Component({
  selector: 'exchange-rate',
  templateUrl: './exchange-rate.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ExchangeRateComponent extends SubscriberComponent implements OnInit {
  public exchangeRateItems: IExchangeRateItem[];

  constructor(private _exchangeRateService: ExchangeRateService,
              private _detectorRef: ChangeDetectorRef) {
    super();
  }

  ngOnInit() {
    this.subscriptions.push(this._exchangeRateService.exchangeRate$.subscribe((result: IExchangeRateItem[]) => {
      this.exchangeRateItems = result;
      this._detectorRef.markForCheck();
    }));
  }

}



