import { ChangeDetectionStrategy, Component, ChangeDetectorRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl, FormControl } from '@angular/forms';
import { AuthService } from '../../services/auth/auth.service';
import { ITokenScheme, ILoginRequest } from '../../services/auth/auth.interfaces';
import { IUser, UserService } from '../../user';
import { Router } from '@angular/router';
import { LoginService } from './login.service';
import { WRONG_PASSWORD_ERROR_MESSAGE, USER_NOT_EXISTS } from './login.constants';
import { SERVER_ERROR_MESSAGE } from '../../constants';


@Component({
  selector: 'login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class LoginComponent {
  form: FormGroup;
  serverErrorTitle: string;
  loading: boolean;

  constructor(private _loginService: LoginService,
    private _authService: AuthService,
    private _userService: UserService,
    private _router: Router,
    private fb: FormBuilder,
    private _detectorRef: ChangeDetectorRef) {
    this._createForm();
  }


  get email() { return this.form.get('email'); }
  get password() { return this.form.get('password'); }

  private _createForm() {
    this.form = this.fb.group({
      email: ['', [Validators.email]],
      password: ['', [Validators.required]],
    });
  }

  regButtonClick() {
    this._router.navigate(['/registration']);
  };

  authorize() {
    if (this.form.valid) {
      const userRequest: ILoginRequest = {
        email: this.form.value.email,
        password: this.form.value.password
      };

      this.loading = true;
      this._loginService.authorize(userRequest).finally(() => {
        this.loading = false;
        this._detectorRef.detectChanges();
      }).subscribe((data: ITokenScheme) => {
        this._authService.storeTokens(data);
        this._userService.getUser();

        this._router.navigate(['/control-panel']);
      },
        (err) => {
          if (err.status === 500) {
            this.serverErrorTitle = SERVER_ERROR_MESSAGE;
          } else {
            this.serverErrorTitle = err.error.description;
          }
          this._detectorRef.detectChanges();
        });
    }
  }

  goToRegistration() {
    this._router.navigate(['/registration']);
  }
}



