import {IBalanceItem} from './balance.interfaces';
import * as balance from './balance.actions';

export interface IBalanceState {
  data: IBalanceItem[],
  loading: boolean
}

export const initialState: IBalanceState = {
  data: null,
  loading: false
};

export function reducer(state = initialState,
                        action: balance.Actions): IBalanceState {
  switch (action.type) {
    case balance.UPDATE_BALANCE: {
       return Object.assign({}, state, {data: action.payload});
    }
    case balance.FETCH_BALANCE: {
      return Object.assign({}, state, {loading: action.payload});
    }
    default: {
      return state;
    }
  }
}



