import { Action } from '@ngrx/store';
import { Screen} from "./layout.interfaces";

export const UPDATE_SCREEN =           '[layout] screen';

export class UpdateScreenAction implements Action {
  readonly type = UPDATE_SCREEN;

  constructor(public payload: Screen) { }
}


export type Actions
  = UpdateScreenAction;



