import { ChangeDetectionStrategy, Component, ChangeDetectorRef, NgZone } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { RegistrationService } from '../registration/registration.service';
import { Router } from '@angular/router';
import * as moment from "moment";
import { ActionType, IConfirmPhoneRequest, IGetConfirmCodeRequest, IGetConfirmCodeResponse } from "../registration/registration.interfaces";
import { SERVER_ERROR_MESSAGE } from '../../constants';
import {UserService} from "../../user";

@Component({
  selector: 'phone-confirmation',
  templateUrl: './phone-confirmation.component.html',
  styleUrls: ['./phone-confirmation.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PhoneConfirmationComponent {
  form: FormGroup;
  serverErrorTitle: string;
  loading: boolean;

  private _millisecondsLeft: number = 0;

  constructor(
    private _registrationService: RegistrationService,
    private _userService: UserService,
    private _router: Router,
    private fb: FormBuilder,
    private _detectorRef: ChangeDetectorRef,
    private _zone: NgZone) {

    this._createForm();
    this.sendConfirmCode();
  }


  get expireDateFormatted() {
    return moment.utc(this._millisecondsLeft).format('mm:ss');
  }

  get hasExpireDate() {
    return this._millisecondsLeft > 0;
  }

  private _startTimerTo(date: number) {
    const expireDate = moment.unix(date);
    let msDifference: number = (<any>expireDate - <any>moment());
    if (msDifference > 0) {
      this._zone.runOutsideAngular(() => {
        const interval = setInterval(() => {
          msDifference -= 1000;
          if (msDifference > 0) {
            this._millisecondsLeft = msDifference;
          } else {
            this._millisecondsLeft = 0;
            clearInterval(interval);
          }
          this._detectorRef.detectChanges();
        }, 1000)
      })
    }


  }

  get confirmationCode() {
    return this.form.get('confirmationCode');
  }

  public confirm() {
    if (this.form.valid && !this.loading) {
      const confirmCodeRequest: IConfirmPhoneRequest = {
        confirmationCode: this.form.value.confirmationCode
      };
      this.loading = true;
      this._registrationService.confirmByPhone(confirmCodeRequest)
        .finally(() => {
          this.loading = false;
          this._detectorRef.detectChanges();
        })
        .subscribe(() => {
          this._userService.confirmUserPhone();
          this._router.navigate(['/control-panel']);
        },
          (err) => {
            if (err.status === 500) {
              this.serverErrorTitle = SERVER_ERROR_MESSAGE;
            } else {
              this.serverErrorTitle = err.error.description;
            }
            this._detectorRef.detectChanges();
          });
    }
  }

  private _createForm() {
    this.form = this.fb.group({
      confirmationCode: ['', [Validators.required, Validators.minLength(4)]]
    });
  }

  public sendConfirmCode() {
    const getCodeReq: IGetConfirmCodeRequest = {
      actionType: ActionType.NewPhone
    };
    this._registrationService.sendRetrieveConfirmCodeRequest(getCodeReq).subscribe((res: IGetConfirmCodeResponse) => {
      this._startTimerTo(res.sendingExpireDate); //res.sendingExpireDate
    }, (err) => {
      if (err.status === 429) { //too many requests
        this._startTimerTo(err.error.sendingExpireDate);  //just show for user how much time left to give him resending code ability
      }
      else if (err.status === 500) {
        this.serverErrorTitle = SERVER_ERROR_MESSAGE;
      }
    });
  }
}



