import { Action } from '@ngrx/store';
import {IBalanceItem} from './balance.interfaces';

export const UPDATE_BALANCE =           '[Balance] update';
export const FETCH_BALANCE =           '[Balance] fetch';

export class UpdateBalanceAction implements Action {
  readonly type = UPDATE_BALANCE

  constructor(public payload: IBalanceItem[]) { }
}

export class FetchBalanceLoadingAction implements Action {
  readonly type = FETCH_BALANCE;

  constructor(public payload: boolean = true) { }
}

export type Actions
  = UpdateBalanceAction
  | FetchBalanceLoadingAction;



