import {Injectable} from '@angular/core';
import {Store} from '@ngrx/store';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/distinctUntilChanged';
import {IExchangeRateItem} from './exchange-rate.interfaces';
import {FetchExchangeRateLoadingAction, UpdateExchangeRateAction} from './exchange-rate.actions';
import {RATES} from './exchange-rate-mock-data';


@Injectable()
export class ExchangeRateService {
  public exchangeRate$: Observable<IExchangeRateItem[]>;
  public balanceLoading$: Observable<boolean>;
  constructor(private _store: Store<any>) {
    const exchangeRateState$ = this._store.select('exchangeRate');
    this.exchangeRate$ = exchangeRateState$.map((x) => x.data).distinctUntilChanged();
    this.getExchangeRate();
  }

  public getExchangeRate() {
    //http request for data
    this._store.dispatch(new FetchExchangeRateLoadingAction(true));
    setTimeout(() => {
      this._store.dispatch(new UpdateExchangeRateAction(RATES));
      this._store.dispatch(new FetchExchangeRateLoadingAction(false));
    }, 1000);

  }
}



