import {Injectable} from "@angular/core";
import {Observable} from "rxjs";
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {ITokenScheme} from "./auth.interfaces";
import {TOKENS_STORAGE_KEY} from "./auth.constants";
import {UserService} from "../../user";
import {getValue} from "../../utils";
import {API_URLS} from "../../constants";
import {Router} from "@angular/router";
import 'rxjs/add/operator/finally';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';

@Injectable()
export class AuthService {
  constructor(private _http: HttpClient,
              private _userService: UserService,
              private _router: Router) {
  }

  public getAuthToken() {
    const storeTokens = this.getStoreTokens();
    if (storeTokens) {
      return storeTokens.authToken;
    }
    return null;
  }

  public getRefreshToken() {
    const storeTokens = this.getStoreTokens();
    if (storeTokens) {
      return storeTokens.refreshToken;
    }
    return null;
  }

  public logout() {
    this._userService.updateUser(null);
    this.removeStoreTokens();
    this._router.navigate(['/login']);

  }

  public removeStoreTokens() {
    localStorage.removeItem(TOKENS_STORAGE_KEY);
  }

  public storeTokens(tokens: ITokenScheme) {
    localStorage.setItem(TOKENS_STORAGE_KEY, JSON.stringify(tokens));
  }

  public getStoreTokens(): ITokenScheme {
    return JSON.parse(localStorage.getItem(TOKENS_STORAGE_KEY));
  }

  public refreshAuthToken(): Observable<any> {
    const user = getValue(this._userService.user$);
    if (!this.getStoreTokens() || !user) {
      return ErrorObservable.create('No credentials or user');
    }

    return this._http.put(API_URLS.REFRESH_TOKEN, JSON.stringify({
      id: user.id,
      refresh_token: this.getRefreshToken()
    }));
  }
}



